import Navbar from "../components/navbar";

import Button from "../components/button";
import Table from "../components/table";

const header = ["ID pacco", "Indirizzo", "Città", "Peso", "Raccomandato"];
const list = [
  ["001", "Viale Nino Bixio 95", "Treviso", "100kg", "Sì"],
  ["002", "Cantine del Todaro", "Ponte di Piave", "69kg", "No"],
  ["003", "Via Municipio 16", "Treviso", "202kg", "No"],
  ["004", "Via Levada 20", "Povegliano (TV)", "90kg", "No"]
];

//Nella versione definitiva qui ci sarà un API call al posto degli array

export default function Test() {
  return (
    <body>
      <div style={{ textAlign: "center", backgroundColor: "#383e4e" }}>
        <Navbar />
        <br />
        <h2 className="pack-list">Dettagli sui pacchi</h2>
        <div id="container-table">
          <Table header={header} rows={list} />
          <br />
          <Button
            id="button-table"
            className="Button "
            text="Indietro"
            path="/"
          />
        </div>
      </div>
    </body>
  );
}
